
import React from 'react';
import ShowSummary from './ShowSummary';

const ShowList = ({ shows }) => {
  return (
    <div className="show-list">
      {shows.map(show => {
        const { id, name, summary, premiered, status, image,language} = show.show;
        const imageUrl = image && image.medium ? image.medium : 'https://via.placeholder.com/210x295?text=No+Image';
        return (
          <ShowSummary
            key={id}
            name={name}
            language={language}
            summary={summary}
            image={imageUrl}
            premiered={premiered}
            status={status}
            
          />
        );
      })}
    </div>
  );
};

export default ShowList;


